@import "../../assets/styles/app";

.whatsNext {

  padding-top: 48px;
  padding-bottom: 52px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .topContent {
    @include container;

    .inner {
      @include inner;

      h2.title {

        p {
          @include Hind;
          font-size: 40px;
          line-height: 85%;
          letter-spacing: -0.02em;
          text-align: center;
          color: #fff;

          @include desktop {
            font-size: 100px;
          }

          &:nth-child(2) {
            margin-top: 4px;

            @include desktop {
              margin-top: 0;
            }
          }

          svg {
            position: relative;
            width: 48px;
            height: 48px;
            top: 12px;

            @include desktop {
              width: 64px;
              height: 64px;
              top: 4px;
            }
          }
        }

      }

      .description {
        margin-top: 24px;
        font-size: 18px;
        line-height: 140%;
        text-align: center;
        color: rgba(255, 255, 255, 0.4);

        @include desktop {
          margin-top: 32px;
          width: 596px;
          margin-left: auto;
          margin-right: auto;
        }
      }

    }
  }

  .sliderControl {
    @include onlyMobile(flex);
    justify-content: center;
    margin-top: 32px;

    .btn {
      @include button;
      @include centerRow;
      border: 1px solid #fff;
      border-radius: 32px;
      width: 48px;
      height: 48px;
      transition: 0.3s;

      svg {
        width: 20px;
        height: 20px;

        path {
          transition: 0.3s;
        }
      }

      &:nth-child(1) {
        margin-right: 12px;

        svg {
          transform: rotate(180deg);
        }
      }

      &_disable {
        border-color: rgba(255, 255, 255, 0.2);

        svg {
          path {
            stroke-opacity: 0.2;
          }
        }
      }

    }

  }

  .sliderMobile {
    @include onlyMobile();
    margin-top: 32px;

    .slide {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;

      .label {
        background: rgba(255, 255, 255, 0.1);
        border-radius: 9px;
        padding: 6px 10px;
        font-weight: 500;
        font-size: 18px;
        line-height: 125%;
        text-align: center;
        color: rgba(255, 255, 255, 0.4);
      }

      .items {
        margin-top: 24px;
        padding: 20px;
        border-radius: 20px;
        backdrop-filter: blur(18.100000381469727px);
        background: rgba(255, 255, 255, 0.05);

        .item {
          display: flex;
          align-items: center;

          &:not(:first-child) {
            margin-top: 16px;
          }

          svg {
            width: 19px;
            height: 19px;
          }

          p {
            @include Hind;
            font-size: 16px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: #fff;
            margin-left: 4px;
          }
        }
      }

    }
  }


}