@import "../../assets/styles/app";

.footer {
  @include container;
  padding-bottom: 24px;

  @include desktop {
    padding-bottom: 0;
  }

  .inner {
    @include inner;
    border-radius: 20px;
    backdrop-filter: blur(2px);
    //background: rgba(0, 0, 0, 0.44);
    padding-top: 32px;
    padding-bottom: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);

    @include desktop {
      border-radius: 20px 20px 0 0;
      padding: 64px;
    }

    .top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 24px;

      @include desktop {
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
      }

      & > svg {
        height: 24px;
        width: 99px;

        @include desktop {
          height: 40px;
          width: 165px;
        }
      }

      .socialLinks {
        display: flex;

        @include desktop {
          margin-top: 32px;
        }

        a {
          display: block;
          transition: 0.3s;

          &:not(:first-child) {
            margin-left: 8px;
          }

          &:hover {
            @include desktop {
              opacity: 0.5;
            }
          }

          svg {
            width: 24px;
            height: 24px;
          }
        }
      }
    }

    .bottom {
      margin-top: 32px;

      @include desktop {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
      }

      .links {
        display: flex;
        flex-direction: column;

        @include desktop {
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          grid-template-rows: auto;
          grid-column-gap: 64px;
          grid-row-gap: 16px;
        }

        .link {
          text-decoration: none;
          font-size: 18px;
          line-height: 125%;
          color: #fff;
          transition: 0.3s;
          padding-left: 20px;

          @include desktop {
            color: rgba(255, 255, 255, 0.4);
            padding-left: 0;
          }

          &:hover {
            @include desktop {
              color: #fff;
            }
          }

          &:not(:last-child) {
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            @include desktop {
              padding-bottom: 0;
              border-bottom: none;
            }
          }

          &:not(:first-child) {
            padding-top: 20px;

            @include desktop {
              padding-top: 0;
            }
          }


        }


      }

      .secondLinks {
        padding: 0 20px;
        margin-top: 24px;
        display: flex;
        justify-content: space-between;

        @include desktop {
          padding: 0;
          margin-top: 0;
          justify-content: flex-start;
        }

        p {
          font-size: 16px;
          line-height: 125%;
          color: rgba(255, 255, 255, 0.4);
        }

        a {
          text-decoration: none;
          font-size: 16px;
          line-height: 125%;
          color: rgba(255, 255, 255, 0.4);
          transition: 0.3s;

          &:hover {
            @include desktop {
              color: #FFF;
            }
          }

          @include desktop {
            margin-left: 32px;
          }
        }
      }

    }

  }
}