@import "../../assets/styles/app";

.whoWeAre {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    .title {
      @include Hind;
      font-size: 40px;
      line-height: 85%;
      letter-spacing: -0.02em;
      color: #fff;
      text-align: center;

      @include desktop {
        font-size: 100px;
      }

      svg {
        width: 48px;
        height: 48px;
        position: relative;
        top: 13px;

        @include desktop {
          width: 64px;
          height: 64px;
        }
      }
    }

    .description {
      margin-top: 24px;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        margin-top: 32px;
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .exploreBtn {
      width: 225px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }

    .cards {
      margin-top: 24px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-column-gap: 21px;
      }

      .card {
        padding: 24px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        @include desktop {
          padding: 20px;
          border-bottom: none;
          border-right: 1px solid rgba(255, 255, 255, 0.1);
          display: block;
        }

        p:nth-child(1) {
          font-weight: 500;
          font-size: 16px;
          line-height: 125%;
          text-transform: uppercase;
          color: #fff;

          @include desktop {
            font-size: 20px;
          }
        }

        p:nth-child(2) {
          font-size: 18px;
          line-height: 125%;
          text-transform: lowercase;
          color: rgba(255, 255, 255, 0.32);

          @include desktop {
            margin-top: 128px;
          }

          span {
            text-transform: uppercase;
            color: rgba(255, 255, 255, 0.68);
          }
        }
      }
    }


  }
}