@import "../../assets/styles/app";

.weNeverRunOut {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    .title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 4px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 6px;
          }
        }
      }
    }

    .description {
      margin-top: 24px;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        margin-top: 32px;
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .learnMoreBtn {
      width: 262px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        width: 262px;
      }
    }

    .cards {
      margin-top: 48px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 24px;
      }

      .card {
        border: 1px solid #1a1c1b;
        border-radius: 20px;
        padding: 20px;
        position: relative;
        overflow: hidden;

        &:not(:first-child) {
          margin-top: 20px;

          @include desktop {
            margin-top: 0;
          }
        }

        .cardTitle {
          font-weight: 500;
          font-size: 24px;
          line-height: 125%;
          text-align: center;
          color: #fff;

          @include desktop {
            text-align: left;
          }
        }

        .items {
          margin-top: 20px;

          @include desktop {
            margin-top: 24px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
          }

          .item {
            display: flex;

            &:not(:last-child) {
              border-bottom: 1px solid #1a1c1b;
              padding-bottom: 20px;
            }

            &:not(:first-child) {
              padding-top: 20px;
            }

            svg {
              width: 24px;
              height: 24px;
              flex-shrink: 0;
            }

            p {
              margin-left: 8px;
              font-size: 18px;
              line-height: 125%;
              color: rgba(255, 255, 255, 0.4);

              span {
                color: #fff;
              }

              @include desktop {
                width: 353px;
              }
            }
          }
        }

        img {
          display: block;
          margin-top: 20px;
          margin-left: auto;
          margin-right: auto;

          @include desktop {
            margin-top: 0;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
          }
        }

        &:nth-child(1) {
          img {
            width: 277px;
            height: 188px;

            @include desktop {
              width: 474px;
              height: 322px;
              right: -165px;
            }
          }
        }

        &:nth-child(2) {
          img {
            width: 244px;
            height: 204px;

            @include desktop {
              width: 369px;
              height: 309px;
              right: -70px;
            }
          }
        }

      }
    }

  }

}