@import "../../assets/styles/app";

.presaleStages {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 58px;
    padding-bottom: 70px;
  }

  .inner {
    @include inner;

    h2.title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 24px;
      line-height: 125%;
      text-align: center;
      color: #fff;

      @include desktop {
        font-size: 40px;
      }
    }


  }
}