@import "../../assets/styles/app";

.havingTrouble {
  @include container;
  padding-top: 48px;
  padding-bottom: 48px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 32px;
  }


  .inner {
    @include inner;

    .label {
      background: rgba(255, 255, 255, 0.1);
      border-radius: 999px;
      padding: 10px 24px;
      font-weight: 500;
      font-size: 16px;
      line-height: 125%;
      text-align: center;
      color: #fff;
      width: 250px;
      margin-left: auto;
      margin-right: auto;
    }

    h2.title {
      margin-top: 24px;

      @include desktop {
        margin-top: 32px;
      }

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 120px;
        }

        &:nth-child(2) {
          margin-top: 4px;

          @include desktop {
            margin-top: 16px;
          }
        }

        svg {
          position: relative;
          width: 32px;
          height: 32px;
          top: 6px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 4px;
          }
        }
      }

    }

    .description {
      margin-top: 24px;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        margin-top: 32px;
        width: 277px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .contactUsBtn {
      border-radius: 32px;
      width: 200px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}