@import "../../assets/styles/app";

.madeByTraders {
  @include container;
  position: relative;
  z-index: 4;
  padding-top: 48px;

  @include desktop {
    padding-top: 128px;
  }

  .inner {
    @include inner;

    .title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 4px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 3px;
          }
        }
      }
    }

    .description {
      margin-top: 24px;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        margin-top: 32px;
        width: 596px;
        margin-left: auto;
        margin-right: auto;
      }
    }

    .exploreBtn {
      margin-top: 32px;
      width: 200px;
      height: 60px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        width: 225px;
      }

    }

    .cards {
      margin-top: 48px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-gap: 21px;
      }

      .card {
        position: relative;
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        background: transparent;
        padding: 20px;
        transition: 0.3s background-color;
        display: flex;
        flex-direction: column;

        &:not(:first-child) {
          margin-top: 16px;

          @include desktop {
            margin-top: 0;
          }
        }

        @include desktop {
          height: 478px;
          justify-content: space-between;
        }

        &:hover {
          @include desktop {
            background-color: rgba(255, 255, 255, 0.1);
          }
        }

        .top {
          display: flex;
          justify-content: space-between;

          .label {
            font-weight: 500;
            font-size: 18px;
            line-height: 125%;
            color: #fff;

            @include desktop {
              font-size: 20px;
              width: 173px;
            }
          }

          .count {
            font-size: 16px;
            line-height: 125%;
            color: rgba(255, 255, 255, 0.5);

            @include desktop {
              font-size: 18px;
            }
          }
        }

        img {
          display: block;
          align-self: center;
          margin-top: 20px;
          height: 200px;
          width: auto;

          @include desktop {
            position: absolute;
            margin-top: 0;
            left: 50%;
            top: 220px;
            transform: translate(-50%, -50%);
          }
        }

        .bottom {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          margin-top: 20px;

          @include desktop {
            margin-top: 0;
          }

          .bottomDescription {
            font-size: 18px;
            line-height: 140%;
            color: rgba(255, 255, 255, 0.4);
          }

          .link {
            display: block;
            text-decoration: none;
            margin-top: 8px;
            font-size: 18px;
            line-height: 140%;
            color: #fff;
            transition: 0.3s;
            border-bottom: 1px solid transparent;

            &:hover {
              @include desktop {
                border-bottom: 1px solid #FFF;
              }
            }

            &_mouseDown {
              color: #ffea0e;
              border-bottom: 1px solid #ffea0e;

              &:hover {
                border-bottom: 1px solid #ffea0e;
              }
            }
          }
        }

        &:nth-child(1) {
          img {
            @include desktop {
              width: 204px;
              height: 198px;
            }
          }
        }

        &:nth-child(2) {
          img {
            @include desktop {
              width: 255px;
              height: 218px;
            }
          }
        }

        &:nth-child(3) {
          img {
            @include desktop {
              width: 236px;
              height: 196px;
            }
          }
        }

        &:nth-child(4) {
          img {
            @include desktop {
              width: 275px;
              height: 220px;
            }
          }
        }

        &:nth-child(5) {
          img {
            @include desktop {
              width: 239px;
              height: 204px;
            }
          }
        }

        &:nth-child(6) {
          img {
            @include desktop {
              width: 210px;
              height: 196px;
            }
          }
        }

        &:nth-child(7) {
          img {
            @include desktop {
              width: 264px;
              height: 222px;
            }
          }
        }


      }
    }

  }
}