@import "../../assets/styles/app";

.joinTheFix {
  @include container;
  position: relative;
  padding-top: 128px;

  @include desktop {
    padding-top: 136px;
  }

  .back {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
  }

  .inner {
    @include inner;
    position: relative;
    z-index: 2;

    .label {
      padding: 8px 24px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 999px;

      @include desktop {
        margin-left: auto;
        margin-right: auto;
        width: 370px;
      }

      p {
        font-weight: 500;
        font-size: 16px;
        line-height: 125%;
        color: #fff;
        text-align: center;

        span {
          color: rgba(255, 255, 255, 0.5);
        }
      }

    }

    .title {
      margin-top: 24px;

      @include desktop {
        margin-top: 16px;
      }

      p {
        @include Hind;
        font-size: 52px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 120px;
        }

        &:nth-child(2) {
          margin-top: 6px;

          @include desktop {
            margin-top: 0;
          }

          svg {
            width: 48px;
            height: 48px;

            @include desktop {
              width: 65px;
              height: 65px;
            }
          }

        }
      }
    }

    .content {
      margin-top: 32px;

      @include desktop {
        margin-top: 48px;
        display: flex;
        flex-direction: row-reverse;
        border: 1px solid rgba(255, 255, 255, 0.1);
        border-radius: 20px;
        width: 957px;
        margin-left: auto;
        margin-right: auto;

      }

      .contentTop {
        display: flex;
        flex-direction: column;

        @include desktop {
          width: 364px;
          flex-shrink: 0;
          padding-top: 8px;
          padding-right: 8px;
          padding-bottom: 24px;
        }

        .card {
          backdrop-filter: blur(11.899999618530273px);
          background: rgba(255, 255, 255, 0.1);
          border-radius: 20px;
          padding: 20px;

          .cardHeader {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .buyBtn {
              @include button;
              display: flex;
              align-items: center;
              border-radius: 123px;
              padding: 8px 20px;
              background: #fff;

              svg {
                width: 20px;
                height: 20px;
              }

              span {
                @include Hind;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: #010302;
                margin-left: 6px;
              }
            }

            & > p {
              display: flex;
              align-items: center;
              padding: 0px 20px;

              svg {
                width: 20px;
                height: 20px;
              }

              span {
                @include Hind;
                font-size: 16px;
                line-height: 140%;
                color: rgba(255, 255, 255, 0.4);
                margin-left: 6px;
              }
            }
          }

          .cardTitle {
            margin-top: 24px;
            font-weight: 500;
            font-size: 24px;
            line-height: 125%;
            text-align: center;
            color: #fff;
          }

          .currency {
            margin-top: 8px;
            @include Hind;
            font-size: 16px;
            line-height: 140%;
            text-align: center;
            color: rgba(255, 255, 255, 0.4);
          }

          .line {
            position: relative;
            margin-top: 32px;
            height: 7px;
            background: rgba(255, 255, 255, 0.1);
            border-radius: 999px;

            .lineInner {
              position: relative;
              height: 100%;
              width: 75%;
              background: linear-gradient(270deg, #dcff06 0%, rgba(226, 253, 38, 0) 100%);
              border-radius: 0 999px 999px 0;

              &::after {
                position: absolute;
                right: 0;
                top: 0;
                height: 100%;
                width: 7px;
                content: "";
                display: block;
                border-radius: 50%;
                filter: blur(9.399999618530273px);
                background: #faec5a;
              }
            }
          }

          .lineDescription {
            margin-top: 12px;
            display: flex;
            justify-content: space-between;

            p {
              font-weight: 500;
              font-size: 16px;
              line-height: 125%;
              color: #fff;
            }
          }

          .info {
            margin-top: 24px;
            display: flex;
            align-items: center;

            svg {
              width: 16px;
              height: 16px;
            }

            span {
              @include Hind;
              font-size: 14px;
              line-height: 125%;
              color: rgba(255, 255, 255, 0.45);
              margin-left: 4px;
            }
          }

          .form {
            margin-top: 8px;

            .payField {
              height: 70px;
              border: 1px solid rgba(255, 255, 255, 0.1);
              border-radius: 20px;
              padding-left: 20px;
              padding-right: 15px;
              display: flex;
              align-items: center;
              justify-content: space-between;

              .left {

                p:nth-child(1) {
                  font-size: 14px;
                  line-height: 125%;
                  color: rgba(255, 255, 255, 0.5);
                }

                p:nth-child(2) {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 125%;
                  color: #fff;
                }
              }
            }

            .receiveField {
              margin-top: 8px;

            }

            .submitBtn {
              margin-top: 24px;
              height: 60px;
            }

          }

          .howToBuyLink {
            display: block;
            margin-top: 12px;
            text-decoration: none;
            @include Hind;
            font-size: 16px;
            line-height: 120%;
            letter-spacing: -0.02em;
            text-align: center;
            color: rgba(255, 255, 255, 0.5);
            transition: 0.3s;

            &:hover {
              @include desktop {
                color: rgba(255, 255, 255, 1);
              }
            }

          }

        }

        .viewLeaderboard {
          align-self: center;
          margin-top: 24px;
          display: flex;
          align-items: center;
          text-decoration: none;

          svg {
            width: 24px;
            height: 24px;
          }

          p {
            @include Hind;
            font-weight: 500;
            font-size: 18px;
            line-height: 120%;
            letter-spacing: -0.02em;
            color: #fff;
            margin-left: 6px;
          }
        }

      }

      .contentBottom {
        margin-top: 48px;

        @include desktop {
          margin-top: 0;
          flex-grow: 1;
        }

        .descriptionTop {

          @include desktop {
            padding-top: 24px;
            padding-left: 24px;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

          }

          p {
            font-size: 18px;
            line-height: 140%;
            text-align: center;
            color: rgba(255, 255, 255, 0.4);

            @include desktop {
              width: 497px;
              text-align: left;
            }
          }

          span {
            color: #fff;
          }


        }

        .groups {
          margin-top: 24px;

          @include desktop {
            margin-top: 0;
          }

          .group {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);

            @include desktop {
              justify-content: flex-start;
              padding-bottom: 26px;
              padding-left: 24px;
            }

            &:not(:first-child) {
              padding-top: 16px;
            }

            &:first-child {
              @include desktop {
                padding-top: 26px;
              }
            }

            .left {
              display: flex;
              align-items: center;

              p:nth-child(1) {
                font-weight: 500;
                font-size: 20px;
                line-height: 125%;
                color: #fff;
              }

              p:nth-child(2) {
                @include centerRow;
                width: 57px;
                height: 29px;
                border-radius: 999px;
                background: #f7fa5a;
                margin-left: 12px;

                @include desktop {
                  width: 90px;
                  height: 29px;
                  margin-left: 16px;
                }

                span {
                  font-weight: 500;
                  font-size: 16px;
                  line-height: 140%;
                  letter-spacing: -0.04em;
                  color: #000;

                  &:nth-child(1) {
                    @include onlyDesktop(inline);
                    margin-right: 4px;
                  }
                }

              }
            }

            .right {

              @include desktop {
                display: flex;
                margin-left: 16px;
              }

              p {
                font-size: 18px;
                line-height: 140%;

                &:nth-child(1) {
                  font-weight: 500;
                  color: #fff;
                }

                &:nth-child(2) {
                  font-weight: 400;
                  color: rgba(255, 255, 255, 0.4);
                  margin-left: 6px;
                }
              }
            }

          }


        }

        .descriptionBottom {
          @include Hind;
          font-size: 18px;
          line-height: 140%;
          text-align: center;
          color: rgba(255, 255, 255, 0.4);
          margin-top: 24px;

          @include desktop {
            width: 497px;
            margin-top: 20px;
            padding-left: 24px;
            padding-bottom: 24px;
            text-align: left;
          }

        }


      }

    }

  }
}