@import "../../assets/styles/app";

.createAnAccount {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 64px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.1);
    padding: 20px 0px 24px;

    @include desktop {
      width: 1000px;
      padding: 32px;
    }

    img {
      display: block;
      width: 289px;
      height: 204px;
      margin: 0 auto;

      @include desktop {
        margin: 0;
        width: 586px;
        height: 447px;
        position: absolute;
        top: -143px;
        right: -244px;
      }

    }

    .title {
      font-weight: 500;
      font-size: 24px;
      line-height: 125%;
      text-align: center;
      color: #fff;
      margin-top: 20px;

      @include desktop {
        font-size: 32px;
        text-align: left;
        margin-top: 0;
      }
    }

    .description {
      margin-top: 8px;
      font-size: 18px;
      line-height: 130%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      @include desktop {
        text-align: left;
        width: 450px;
      }

      span {
        color: #FFF;
      }
    }

    .signBtn {
      margin-top: 24px;
      width: 194px;
      height: 60px;
      margin-left: auto;
      margin-right: auto;

      @include desktop {
        margin-top: 72px;
        margin-left: 0;
        margin-right: 0;
      }
    }

    svg {
      @include onlyDesktop();
      position: absolute;
      width: 200px;
      height: 200px;

      &:nth-child(1) {
        left: 340px;
        bottom: 0;
      }

      &:nth-child(2) {
        left: 470px;
        top: -116px;
      }
    }


  }
}