@import "../../assets/styles/app";

.fixToken {
  @include container;
  padding-top: 48px;
  padding-bottom: 24px;

  @include desktop {
    padding-top: 128px;
    padding-bottom: 64px;
  }

  .inner {
    @include inner;

    .title {

      p {
        @include Hind;
        font-size: 40px;
        line-height: 85%;
        letter-spacing: -0.02em;
        text-align: center;
        color: #fff;

        @include desktop {
          font-size: 100px;
        }

        &:nth-child(2) {
          margin-top: 4px;

          @include desktop {
            margin-top: 0;
          }
        }

        svg {
          position: relative;
          width: 48px;
          height: 48px;
          top: 12px;

          @include desktop {
            width: 64px;
            height: 64px;
            top: 4px;
          }
        }
      }
    }

    .descriptionMobile {
      @include onlyMobile;
      margin-top: 24px;
      font-size: 18px;
      line-height: 140%;
      text-align: center;
      color: rgba(255, 255, 255, 0.4);

      span {
        color: #fff;
      }
    }

    .viewBtn {
      width: 227px;
      height: 60px;
      margin-top: 32px;
      margin-left: auto;
      margin-right: auto;
    }

    .tokenDistribution {
      margin-top: 48px;

      @include desktop {
        margin-top: 96px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
      }

      .top {

        @include desktop {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
        }

        .label {
          @include Hind;
          font-weight: 500;
          font-size: 24px;
          line-height: 125%;
          text-align: center;
          color: #fff;

          @include desktop {
            font-size: 64px;
            width: 312px;
            text-align: left;
          }
        }

        .descriptionDesktop {
          @include onlyDesktop;
          font-size: 18px;
          line-height: 140%;
          color: rgba(255, 255, 255, 0.4);
          width: 472px;

          span {
            color: #fff;
          }
        }

      }

      .bottom {
        margin-top: 32px;

        @include desktop {
          margin-top: 0;
          border: 1px solid rgba(255, 255, 255, 0.1);
          border-radius: 42px;
          padding: 60px 40px 32px;
        }

        .line {
          height: 4px;
          display: flex;

          .lineItem {
            position: relative;
            height: 100%;

            &::after {
              @include onlyDesktop();
              content: "";
              position: absolute;
              left: 50%;
              top: 0;
              transform: translate(-50%, -100%);
              height: 8px;
              width: 1px;
              background: #FFF;
            }

            .value {
              @include onlyDesktop;
              position: absolute;
              left: 50%;
              bottom: 12px;
              transform: translateX(-50%);
              font-weight: 500;
              font-size: 16px;
              line-height: 125%;
            }

            div {
              @include onlyDesktop;
              position: absolute;
              height: 164px;
              left: 0;
              width: 100%;
              bottom: 0;
              transform: translateY(100%);
            }

            .label {
              @include onlyMobile();
              position: absolute;
              font-size: 16px;
              line-height: 125%;
              color: rgba(255, 255, 255, 0.4);
              left: 0;
              bottom: -8px;
              transform: translateY(100%);
            }


          }
        }

        .info {
          margin-top: 54px;

          @include desktop {
            margin-top: 204px;
            display: flex;
          }

          .infoLabel {
            font-weight: 500;
            font-size: 20px;
            line-height: 125%;
            color: #fff;

            @include desktop {
              font-weight: 400;
              font-size: 18px;
            }
          }

          .left {
            @include onlyDesktop();
            padding: 12px 0;
            border-right: 1px solid rgba(255, 255, 255, 0.1);
            width: 220px;

            .leftItems {
              margin-top: 16px;

              .item {
                display: flex;
                align-items: center;

                &:not(:first-child) {
                  margin-top: 16px;
                }

                div {
                  width: 12px;
                  height: 12px;
                }

                p {
                  margin-left: 12px;
                  font-size: 18px;
                  line-height: 125%;
                  color: rgba(255, 255, 255, 0.4);
                }
              }
            }


          }

          .right {

            @include desktop {
              padding: 12px 0 12px 32px;
              flex-grow: 1;
            }

            .rightItems {
              margin-top: 20px;

              @include desktop {
                margin-top: 16px;
              }

              .item {
                display: flex;
                justify-content: space-between;
                align-items: center;

                &:not(:last-child) {
                  padding-bottom: 12px;
                  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

                  @include desktop {
                    padding-bottom: 0;
                    border-bottom: none;
                  }
                }

                &:not(:first-child) {
                  padding-top: 12px;

                  @include desktop {
                    padding-top: 0;
                  }
                }

                &:not(:first-child) {
                  @include desktop {
                    margin-top: 16px;
                  }
                }

                p:nth-child(1) {
                  font-size: 18px;
                  line-height: 125%;
                  color: rgba(255, 255, 255, 0.4);
                }

                p:nth-child(2) {
                  font-size: 18px;
                  line-height: 125%;
                  font-weight: 500;
                  color: #fff;

                  span {
                    font-weight: 400;
                    color: rgba(255, 255, 255, 0.4);
                  }
                }



              }
            }
          }


        }
      }

    }

  }
}